import { useQuery } from "@tanstack/react-query";
import QuestionService from "../../QuestionService";
import { QUERY_KEY } from "../../../constants/queryKey";

export default function useQuestionRanking() {
  return useQuery(
    [QUERY_KEY.RANKING],
    () => QuestionService.fetchQuestionRanking(),
    {
      staleTime: 1000 * 10,
      cacheTime: 1000 * 10,
      suspense: true,
    }
  );
}
