export const REGEX = {
  email: /[a-z0-9]+@[a-z]+\.[a-z]{2,3}/,
  username: /^[a-z|A-Z|0-9|_.]{4,20}$/,
  password: /^(?=.*\d)(?=.*[a-z])(?=.*[a-zA-Z]).{8,15}$/,
  special: /[^\w\sㄱ-힣]|[_]/g,
};

const validation = {
  isValidLoginInputs(formData) {
    const { username, password } = formData;

    if (username !== "" && password !== "") {
      return true;
    }
    return false;
  },
  isValidSignUpInputs(formData) {
    const { username, email, password, password2, verification_code } = formData;

    if (username === "") {
      alert("아이디를 입력해주세요.");
      return false;
    }

    if (!REGEX.username.test(username)) {
      alert("아이디를 확인해주세요.");
      return false;
    }

    if (email === "") {
      alert("이메일을 입력해주세요.");
      return false;
    }

    if (!REGEX.email.test(email)) {
      alert("이메일 형식을 확인해주세요.");
      return false;
    }

    console.log(String(verification_code))
    if (String(verification_code).length !== 6) {
      alert("이메일 코드를 확인해주세요.");
      return false;
    }

    if (!REGEX.password.test(password)) {
      alert("비밀번호를 확인해주세요");
      return false;
    }

    if (password !== password2) {
      alert("비밀번호가 일치하지 않습니다.")
      return false;
    }

    if (!verification_code) {
      alert("이메일 인증코드를 확인해주세요.");
      return false;
    }

    return true;
  },
  isValidEmail(email) {
    return REGEX.email.test(email);
  },
  isValidPassword(password) {
    return REGEX.password.test(password);
  },
  isValidUsername(username) {
    return REGEX.username.test(username);
  },
  filterUsername(username) {
    return username.replace(REGEX.username, ``)
  }
}

export default validation;