import css from "./Landing.module.css";
import Button from "../../components/Button/Button";
import Logo from "../../components/Logo/Logo";
import { useNavigate } from "react-router-dom";
import Corporation from "../../components/Logo/Corporation";
import { useAuth } from "../../context/AuthContext";
import useLogout from "../../api/queries/member/useLogout";

export default function Landing() {
  const navigate = useNavigate();
  const [{ isLogin }] = useAuth();
  const { refetch: logoutRequest } = useLogout();

  return (
    <div className={css.div}>
      <Logo />
      <p>CHATTY</p>
      <Button
        role="landingLogin"
        onClick={() => {
          navigate("/login");
        }}
      >
        {isLogin ? "프로필" : "로그인"}
      </Button>
      {!isLogin && <Button role="landingSignUp" onClick={() => navigate("/signup")}>
        회원가입
      </Button>}
      {!isLogin && (
        <Button role="landingFind" onClick={() => navigate("/find")}>
          계정찾기
        </Button>
      )}
      {isLogin && (
        <Button role="landingFind" onClick={logoutRequest}>
          로그아웃
        </Button>
      )}
      <Corporation />
    </div>
  );
}
