import React, { useEffect, useCallback, useMemo } from "react";
import SnbView from "./SnbView";
import { useModal } from "../../context/ModalContext";
import { useNavigate } from "react-router-dom";
import isLogin from "../../utils/isLogin";
import { enableScroll, disableScroll } from "../../utils/scroll";
import CardModal from "../Modal/CardModal/CardModal";
import { useAuth } from "../../context/AuthContext";

function Snb() {
  const navigate = useNavigate();
  const [{ currentUser }] = useAuth();
  const [type, { open, close }] = useModal();

  useEffect(() => {
    disableScroll();
    return enableScroll;
  }, []);

  const closeSnb = useCallback(() => {
    close();
  }, [close]);

  const restrictedUrls = useMemo(() => {
    return {
      "/mypage": true,
      "/chatlist": true,
    };
  }, []);

  const goHome = useCallback(() => {
    if (currentUser) {
      closeSnb();
      navigate(`/${currentUser}`);
      return;
    }
    closeSnb();
    navigate("/");
  }, [navigate, currentUser, closeSnb]);

  const goTo = useCallback(
    (url) => {
      if (restrictedUrls[url]) {
        console.log(!isLogin());
        if (!isLogin()) {
          open(
            <CardModal
              role="confirm"
              title="로그인 하시겠습니까?"
              desc="해당 서비스는 로그인 후 이용가능합니다."
              buttonText={{ cancel: "취소", confirm: "확인" }}
              onClose={close}
              onSuccess={() => {
                navigate("/login");
                close();
              }}
            />
          );
          return;
        }
      }
      closeSnb();
      navigate(url);
    },
    [open, close, navigate, restrictedUrls, closeSnb]
  );

  const props = {
    type,
    goTo,
    goHome,
    closeSnb,
  };

  return <SnbView {...props} />;
}

export default React.memo(Snb);
