import React, { useEffect, useState, useCallback, useMemo } from "react";
import ProfileView from "./ProfileView";
import useAnswered from "../../hooks/useAnswered";
import useUnAnswered from "../../hooks/useUnAnswered";
import useRejected from "../../hooks/useRejected";
import { useQuestionContext } from "../../context/QuestionContext";
import { useLoaderData, useParams } from "react-router-dom";
import useLocalStorage from "../../utils/useLocalStorage";
import useIntersect from "../../hooks/useIntersect";
import useToast from "../../hooks/useToast";
import useWebShareApi from "../../hooks/useWebShareApi";

function Profile() {
  const profile = useLoaderData();
  const openToast = useToast();
  const { username } = useParams();
  const [context, dispatch] = useQuestionContext();
  const [questionType, setQuestionType] = useState("answered");
  const [isIntersecting, setIsIntersecting] = useState(false);
  const shareURL = useWebShareApi();

  useEffect(() => {
    dispatch({ type: "INIT_QUESTION_COUNT", payload: profile.question_count });
  }, [dispatch, profile.question_count]);

  let nextPage = context[questionType].next;
  let questions = context[questionType].results;
  let questionCount = context.questionCount;
  const currentUser = useMemo(
    () => useLocalStorage.get(import.meta.env.VITE_NICKNAME),
    []
  );
  let isCurrentUser = useMemo(
    () => username === currentUser,
    [username, currentUser]
  );
  let isAlreadyFollowed = useMemo(
    () => profile.follower.indexOf(currentUser) > -1,
    [profile.follower, currentUser]
  );

  const shareProfileURL = useCallback(() => {
    shareURL();
  }, [shareURL]);

  const onIntersect = (entry) => {
    if (!nextPage) {
      return;
    }
    if (isIntersecting) {
      switch (questionType) {
        case "answered":
          useAnswered(dispatch, username, context["answered"].next);
          break;
        case "unAnswered":
          useUnAnswered(dispatch, context["unAnswered"].next);
          break;
        case "rejected":
          useRejected(dispatch, context["rejected"].next);
          break;
        default:
          throw new Error("questionType을 확인해주세요.");
      }
    }
    setIsIntersecting(entry.isIntersecting);
  };

  const onCountClick = useMemo(() => {
    return {
      showAnswered() {
        setQuestionType("answered");
      },
      showUnAnswered() {
        if (!isCurrentUser) {
          openToast("본인만 확인할 수 있습니다.");
          return;
        }
        setQuestionType("unAnswered");
      },
      showRejected() {
        if (!isCurrentUser) {
          openToast("본인만 확인할 수 있습니다.");
          return;
        }
        setQuestionType("rejected");
      },
    };
  }, [isCurrentUser, openToast]);

  const pageBottomRef = useIntersect(onIntersect);

  const props = {
    profile,
    questionCount,
    questionType,
    onCountClick,
    questions,
    isCurrentUser,
    isAlreadyFollowed,
    pageBottomRef,
    isIntersecting,
    shareProfileURL,
    nextPage,
  };

  return <ProfileView {...props} />;
}

export default React.memo(Profile);
