import { useMutation } from "@tanstack/react-query";
import { useNavigate } from "react-router-dom";
import AuthService from "../../auth/AuthService";
import { useAuth } from "../../../context/AuthContext";
import { useToast2 } from "../../../context/ToastContext";

export default function useSignup(input) {
  const navigate = useNavigate();
  const [, { login }] = useAuth();
  const openToast = useToast2();

  return useMutation(() => AuthService.signup(input), {
    onSuccess: ({ data }) => {
      login(data);
      navigate(`/${data.username}`);
    },
    onError: (error) => {
      console.log("ERROR :: ", error);
      if (error.response.status === 400) {
        const errorMessage = error.response.data;
        if (errorMessage.error) {
          openToast(errorMessage.error);
          return;
        }
        if (errorMessage.username) {
          if (
            errorMessage.username[0] ===
            "Ensure this field has at least 5 characters."
          ) {
            openToast("아이디를 최소 4자리 이상 입력해주세요.");
            return;
          }
          if (
            errorMessage.username[0] ===
            "Ensure this field has no more than 15 characters."
          ) {
            openToast("아이디를 20자리 이하로 입력해주세요.");
            return;
          }
        }
        if (errorMessage.password) {
          if (errorMessage.password[0] === "This password is too common.") {
            openToast("더 안전한 비밀번호를 사용해주세요.");
          }
          if (errorMessage.password[0] === "비밀번호가 일치하지 않습니다.") {
            openToast("비밀번호가 일치하지 않습니다.");
          }
          if (
            errorMessage.password[0] ===
            "Ensure this field has no more than 20 characters."
          ) {
            openToast("비밀번호를 20자리 이하로 입력해주세요.");
          }
          return;
        }
      }
    },
    retry: 0,
  });
}

