import { useMemo } from "react";
import css from "./Ranking.module.css";
import { Link } from "react-router-dom";
import RankingSvg from "../../components/Svg/RankingSvg";
import NoQuestionUI from "../../components/Question/NoQuestionUI";
import useQuestionRanking from "../../api/queries/question/useQuestionRanking";

export default function Ranking() {
  const { data } = useQuestionRanking();
  const ranking = data.data;
  const top3 = ranking.slice(0, 3);
  const top10 = ranking.slice(3);

  return (
    <>
      <div className={css.RankingPage}>
        <TopRank top3={top3} />
        <RankingList top10={top10} />
      </div>
    </>
  );
}

function TopRank({ top3 }) {
  return (
    <div className={css.TopRank}>
      <div className={css.RankWrap}>
        {top3.length === 0
          ? null
          : top3.map((ranker, idx) => (
              <TopRankItem key={idx + 1} ranker={ranker} ranking={idx + 1} />
            ))}
      </div>
    </div>
  );
}

function TopRankItem({ ranker, ranking }) {
  const size = useMemo(
    () => ({
      1: "first",
      2: "secound",
      3: "third",
    }),
    []
  );

  return (
    <div className={`${css[size[ranking]]}`}>
      <Link to={`/${ranker.username}`}>
        <div
          className={`${
            ranking === 1 ? css["avatarLarge"] : css["avatarMedium"]
          }`}
        >
          <img
            src={ranker.profile_image}
            className={css.avatarImage}
            alt="프로필사진"
          />
          <div className={css.award}>
            <RankingSvg />
          </div>
        </div>

        <div className={css.flex}>
          <div className={css.username}>
            <p className={css.test}>{ranker.username}</p>
          </div>
          <div className={css.questionCount}>{ranker.question_count}개</div>
        </div>
      </Link>
    </div>
  );
}

/////////// 리스트 0일떄도 생각
function RankingList({ top10 }) {
  return (
    <ul className={css.RankingList}>
      {top10.length === 0 ? (
        <NoQuestionUI />
      ) : (
        top10.map((ranker, idx) => (
          <RankingListItem key={idx + 4} ranker={ranker} ranking={idx + 4} />
        ))
      )}
    </ul>
  );
}

function RankingListItem({ ranker, ranking }) {
  return (
    <Link to={`/${ranker.username}`}>
      <li className={css.RankingListItem}>
        <div className={css.index}>{ranking}</div>
        <div className={css.avatarSmall}>
          <img
            src={ranker.profile_image}
            className={css.avatarImage}
            alt="프로필사진"
          />
        </div>
        <div className={css.usernameSmall}>
          <p className={css.test}>{ranker.username}</p>
        </div>
        <div className={css.questionCount}>{ranker.question_count}개</div>
      </li>
    </Link>
  );
}
