import React from "react";
import css from "../Login/Login.module.css";
import { useNavigate } from "react-router-dom";
import Input from "../../components/Input/Input";
import Button from "../../components/Button/Button";
import Warning from "../../components/Warning/Warning";
import Toast from "../../components/Toast/Toast";
import EyeSvg from "../../components/Svg/EyeSvg";
import EyeSlash from "../../components/Svg/EyeSlash";
import Logo from "../../components/Logo/Logo";
import Corporation from "../../components/Logo/Corporation";

export default function SignUpView({
  input,
  onInput,
  onSubmit,
  toastState,
  isLoading,
  sendEmailCode,
  sendDuplicateNameCheck,
  duplicateBtnDisable,
  emailCodeDisable,
  isInValidUserName,
  isInvalidEmail,
  isInValidPassword,
  isInValidConfirmPassword,
  passVisibility,
  togglePassVisibility,
}) {
  return (
    <main>
      <section className={css.loginPage}>
        <Logo />
        <form className={css.inputs} onSubmit={onSubmit}>
          <div>
            <div className={css.authRequired}>
              <Input
                type="text"
                name="username"
                value={input.username}
                onChange={onInput}
                placeholder="아이디"
                required={true}
              />
              <Button
                type="button"
                role="emailAuth"
                onClick={sendDuplicateNameCheck}
                disabled={input.username.length < 1 || duplicateBtnDisable}
              >
                중복확인
              </Button>
            </div>
            {input.username.length > 0 && isInValidUserName && (
              <Warning>영문 4~20자리/특수문자._허용</Warning>
            )}
          </div>
          <div className={css.authRequired}>
            <Input
              role="email"
              type="email"
              name="email"
              value={input.email}
              onChange={onInput}
              placeholder="이메일"
              disabled={input.username < 1}
              required={true}
            />
            <Button
              type="button"
              role="emailAuth"
              onClick={sendEmailCode}
              disabled={isInvalidEmail | emailCodeDisable}
            >
              인증코드 받기
            </Button>
          </div>
          <Input
            type="verification_code"
            name="verification_code"
            value={input.verification_code}
            onChange={onInput}
            placeholder="이메일 인증코드"
            required={true}
          />
          <div>
            <div className={css.passwordInput}>
              <Input
                type={passVisibility ? "text" : "password"}
                name="password"
                value={input.password}
                onChange={onInput}
                placeholder="비밀번호"
                required={true}
              />
              <div className={css.eye} onClick={togglePassVisibility}>
                {passVisibility ? <EyeSlash /> : <EyeSvg />}
              </div>
            </div>
            {input.password.length > 0 && isInValidPassword && (
              <Warning>문자/숫자 포함 형태의 8~15자리</Warning>
            )}
          </div>
          <div>
            <div className={css.passwordCheckInput}>
              <Input
                type={passVisibility ? "text" : "password"}
                name="password2"
                value={input.password2}
                onChange={onInput}
                placeholder="비밀번호 확인"
                required={true}
              />
              <div className={css.eye2} onClick={togglePassVisibility}>
                {passVisibility ? <EyeSlash /> : <EyeSvg />}
              </div>
            </div>
            {input.password2.length > 0 && isInValidConfirmPassword && (
              <Warning>비밀번호가 일치하지 않습니다.</Warning>
            )}
          </div>
          <MemoizedButtons isLoading={isLoading} />
        </form>
      </section>
      <Corporation />
      {/* hack */}
      {toastState.show && <Toast desc={toastState.desc} />}
    </main>
  );
}

const MemoizedButtons = React.memo(({ isLoading }) => {
  const navigate = useNavigate();

  return (
    <div className={css.btns}>
      <Button
        type="button"
        role="loginBack"
        onClick={() => {
          navigate(-1);
        }}
      >
        이전으로
      </Button>
      <Button
        type="submit"
        role="signup"
        className={css.loginBtn}
        disabled={isLoading}
      >
        {isLoading ? <div className={css.spinner} aria-hidden /> : "회원가입"}
      </Button>
    </div>
  );
});
