import React, { useCallback } from "react";
import css from "./Login.module.css";
import Button from "../../components/Button/Button";
import Input from "../../components/Input/Input";
import Warning from "../../components/Warning/Warning";
import Logo from "../../components/Logo/Logo";
import EyeSvg from "../../components/Svg/EyeSvg";
import EyeSlash from "../../components/Svg/EyeSlash";
import Corporation from "../../components/Logo/Corporation";
import { useNavigate } from "react-router-dom";

export default function LoginView({
  input,
  onInput,
  onSubmit,
  isLoading,
  isValidPassword,
  passVisibility,
  togglePassVisibility,
}) {
  return (
    <main>
      <section className={css.loginPage}>
        <Logo />
        <form onSubmit={onSubmit} className={css.inputs}>
          <div>
            <Input
              name="username"
              value={input.username}
              required={true}
              onChange={onInput}
              placeholder="아이디"
            />
          </div>
          <div>
            <div className={css.passwordInput}>
              <Input
                type={passVisibility ? "text" : "password"}
                name="password"
                value={input.password}
                onChange={onInput}
                required={true}
                placeholder="비밀번호"
              />
              <div className={css.eye} onClick={togglePassVisibility}>
                {passVisibility ? <EyeSlash /> : <EyeSvg />}
              </div>
            </div>
            {!isValidPassword && <Warning>영어/숫자/8~15자리</Warning>}
          </div>
          <MemoizedButtons isLoading={isLoading} />
        </form>
      </section>

      <Corporation />
    </main>
  );
}

const MemoizedButtons = React.memo(({ isLoading }) => {
  const navigate = useNavigate();
  const goBack = useCallback(() => navigate(-1), [navigate]);

  return (
    <div className={css.btns}>
      <Button type="button" role="loginBack" onClick={goBack}>
        이전으로
      </Button>
      <Button type="submit" role="login" disabled={isLoading}>
        {isLoading ? <div className={css.spinner} /> : "로그인"}
      </Button>
    </div>
  );
});
