import React, { useState, useMemo, useCallback } from "react";
import LoginView from "./LoginView";
import useInput from "../../hooks/useInput";
import validation from "../../utils/validation";
import useLogin from "../../api/queries/member/useLogin";

export default function Login() {
  const [passVisibility, setPassVisibility] = useState(false);
  const [input, onInput] = useInput({ username: "", password: "" });
  const { mutate: requestLogin, isLoading } = useLogin(input);

  const onSubmit = useCallback(
    (e) => {
      e.preventDefault();
      requestLogin(input);
    },
    [input, requestLogin]
  );

  const togglePassVisibility = useCallback(() => {
    setPassVisibility((prev) => !prev);
  }, []);

  const isValidPassword = useMemo(
    () =>
      input.password.length === 0 || validation.isValidPassword(input.password),
    [input.password]
  );

  const props = {
    input,
    onInput,
    onSubmit,
    isLoading,
    isValidPassword,
    passVisibility,
    togglePassVisibility,
  };

  return <LoginView {...props} />;
}
