import React, { useState, useCallback } from "react";
import { useLoaderData } from "react-router-dom";
import useInput from "../../hooks/useInput";
import MypageView from "./MypageView";
import { useModal } from "../../context/ModalContext";
import CardModal from "../../components/Modal/CardModal/CardModal";
import useLogout from "../../api/queries/member/useLogout";
import useUpdateMypage from "../../api/queries/member/useUpdateMypage";
import { useQuestionContext } from "../../context/QuestionContext";
import { useAuth } from "../../context/AuthContext";

export default function MyPage() {
  const [, dispatch] = useQuestionContext();
  const loaderData = useLoaderData();
  const { username, profile_image, profile_message } = loaderData;
  const [thumbnail, setThumbnail] = useState(null);
  const [editable, setEditable] = useState(false);
  const [, { open, close }] = useModal();
  const { refetch: logoutRequest } = useLogout();

  const [{ currentUser }] = useAuth();

  const [image, setImage] = useState(profile_image);
  const [input, onInput] = useInput({
    username,
    profile_message,
  });

  const { mutate: updateMypageInfo, isLoading } = useUpdateMypage();

  const editMypage = (e) => {
    e.preventDefault();
    toggleEditable();

    if (!editable) {
      return;
    }

    if (
      image === profile_image &&
      username === input.username &&
      profile_message === input.profile_message
    ) {
      return;
    }

    const formData = new FormData();

    if (input.username && input.username !== currentUser) {
      formData.append("username", input.username);
    }
    if (input.profile_message && input.profile_message !== profile_message) {
      formData.append("profile_message", input.profile_message);
    }
    if (image && image !== profile_image) {
      formData.append("profile_image", image);
    }

    updateMypageInfo(formData);
  };

  const changeImg = (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0]);
    }
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setThumbnail(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };

  const handleLogout = useCallback(() => {
    logoutRequest();
    dispatch({ type: "CLEAR" });
  }, [logoutRequest, dispatch]);

  const openLogoutModal = useCallback(() => {
    open(
      <CardModal
        role="confirm"
        title="로그아웃 하시겠습니까?"
        desc="로그아웃 이후에도 익명으로 질문을 남길수 있습니다."
        buttonText={{ cancel: "취소", confirm: "확인" }}
        onClose={close}
        onSuccess={handleLogout}
      />
    );
  }, [open, close, handleLogout]);

  const toggleEditable = useCallback(() => {
    setEditable((prev) => !prev);
  }, []);

  const props = {
    loaderData,
    editable,
    input,
    onInput,
    thumbnail,
    isLoading,
    changeImg,
    editMypage,
    openLogoutModal,
  };

  return <MypageView {...props} />;
}
